body {
  display: flex;
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
  min-height: 100%;
  margin: 0 auto;
  flex-direction: column;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Lato", sans-serif;
  color: #444444;
}
@media (min-width: 767px) {
  body {
    width: 100%;
  }
}
@media (min-width: 1439px) {
  body {
    width: 100%;
  }
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-header {
  width: 100%;
  z-index: 1;
}
.main-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 13px 20px;
  align-items: center;
  justify-content: space-between;
  z-index: 1;
}
@media (min-width: 767px) {
  .main-header__wrapper {
    padding: 25px 30px 0 30px;
    justify-content: flex-start;
    gap: 94px;
  }
}
@media (min-width: 1439px) {
  .main-header__wrapper {
    padding: 55px 110px 0 110px;
    max-width: 1440px;
    justify-content: space-between;
  }
}
.main-header__title {
  display: contents;
}
@media (min-width: 767px) {
  .main-header__title {
    display: flex;
    align-items: center;
  }
}
.main-nav__item {
  display: block;
}
@media (min-width: 767px) {
  .main-header__logo-text {
    margin: 14px 0;
    margin-left: 13px;
  }
}
@media (min-width: 767px) and (min-width: 1439px) {
  .main-header__logo-text {
    width: 118px;
    height: 21px;
  }
}
.main-nav--closed .main-nav__toggle-button {
  padding: 15px 0;
  padding-left: 10px;
  border: none;
  cursor: pointer;
  background: none;
}
.main-nav--closed .main-nav__toggle-button:hover {
  opacity: 0.7;
}
.main-nav--closed .main-nav__toggle-button:hover:active {
  opacity: 0.5;
}
.main-nav--closed .main-nav__toggle {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 -9px 0 0 #000000, 0 7px 0 0 #000000;
}
@media (min-width: 767px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 320px) {
  .main-nav--nojs .main-nav__toggle-button {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
  .main-nav--nojs .main-nav__item--active .main-nav__title {
    text-align: center;
  }
}
.main-nav--opened .main-nav__toggle-button {
  padding: 19px 0;
  padding-left: 10px;
  border: none;
  cursor: pointer;
  background: none;
}
.main-nav--opened .main-nav__toggle-button:hover {
  opacity: 0.7;
}
.main-nav--opened .main-nav__toggle-button:hover:active {
  opacity: 0.5;
}
.main-nav--opened .main-nav__toggle {
  display: block;
  width: 25px;
  height: 2px;
  background-color: #000000;
  box-shadow: 3px 0 0 0 #000000;
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle-clon {
  display: block;
  width: 25px;
  height: 2px;
  background-color: #000000;
  box-shadow: 3px 0 0 0 #000000;
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__wrapper {
  position: absolute;
  top: 50px;
  right: 0;
  left: 0;
  z-index: 10;
}
@media (min-width: 767px) {
  .main-nav--opened .main-nav__wrapper {
    display: contents;
  }
}
@media (min-width: 767px) {
  .main-nav__item--active::after {
    content: "";
    position: relative;
    display: block;
    top: 7px;
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 767px) and (min-width: 1439px) {
  .main-nav__item--active::after {
    background-color: none;
    border-bottom: 2px solid #ffffff;
  }
}
.main-nav__list {
  width: 100%;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 767px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
    gap: 34px;
    margin-top: 3px;
  }
}
@media (min-width: 1439px) {
  .main-nav__list {
    display: flex;
    z-index: 1;
    background-color: transparent;
  }
}
.site-list .main-nav__title {
  display: block;
  position: relative;
  margin: 0;
  padding: 22px 0;
  padding-bottom: 24px;
  top: 15px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 767px) {
  .site-list .main-nav__title {
    display: contents;
    font-size: 18px;
    line-height: 24px;
    border: none;
  }
}
@media (min-width: 1439px) {
  .site-list .main-nav__title {
    font-size: 20px;
    line-height: 30px;
    background-color: transparent;
    color: #111111;
  }
}
@media (min-width: 1439px) {
  .site-list .main-nav__title-catalog {
    color: #ffffff;
  }
  .main-nav__item--active-catalog::after {
    content: "";
    position: relative;
    display: block;
    top: 10px;
    border-bottom: 2px solid #68b738;
  }
}
.main-nav {
  display: contents;
}
@media (max-width: 767px) {
  .main-nav {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item,
  .main-header__logo-dasktop,
  .main-header__logo-mobile {
    display: none;
  }
}
@media (min-width: 767px) {
  .main-header__logo,
  .main-nav--opened .main-nav__toggle,
  .main-nav--opened .main-nav__toggle-clon,
  .main-nav__toggle-button {
    display: none;
  }
}
@media (max-width: 1439px) {
  .main-header__logo-dasktop {
    display: none;
  }
}
@media (min-width: 1439px) {
  .main-nav--opened .main-nav__toggle,
  .main-nav--opened .main-nav__toggle-clon,
  .main-nav__toggle-button,
  .main-header__logo,
  .main-header__logo-mobile {
    display: none;
  }
}
